import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import prayTimes from "./assets/prayTimes";
import moment from "moment-timezone";
import "moment/locale/ar";
import VueI18n from "vue-i18n";
import en from "../locales/en.json";
import ar from "../locales/ar.json";
import de from "../locales/de.json";

const tz = moment.tz.guess();
const tzUtcOffset = moment().utcOffset();
const isDST = moment().isDST();
const userTimeZone = {
  label: tz,
  utcOffset: tzUtcOffset + "(min)",
  value: isDST ? tzUtcOffset / 60 - 1 : tzUtcOffset / 60,
};

Vue.config.productionTip = false;
Vue.prototype.prayTimes = prayTimes;
Vue.prototype.userTimeZone = userTimeZone;
Vue.prototype.moment = moment;
Vue.use(VueI18n);

const i18n = new VueI18n({
  locale: "en", // Default locale
  messages: { en, ar, de },
});

new Vue({
  router,
  i18n,
  render: (h) => h(App),
}).$mount("#app");
