<template>
  <div id="app" :class="$i18n.locale === 'ar' ? 'rtl' : ''" v-if="!reload">
    <div id="nav">
      <div>
        <router-link to="/">{{ $t("home") }}</router-link> |
        <router-link to="/month">{{ $t("month") }}</router-link> |
        <router-link to="/settings">{{ $t("settings") }}</router-link>
      </div>
      <div>
        <button @click="changeLocale('ar')">العربية</button>
        <button @click="changeLocale('en')">English</button>
        <button @click="changeLocale('de')">Deutsch</button>
      </div>
    </div>
    <router-view />
  </div>
</template>
<script>
export default {
  name: "App",
  data() {
    return {
      locale: "en",
      reload: false,
    };
  },
  mounted() {
    this.$i18n.locale = this.locale;
    this.moment.locale(this.locale);
  },
  methods: {
    changeLocale(locale) {
      this.locale = locale;
    },
  },
  watch: {
    locale(newValue, oldValue) {
      if (newValue == oldValue) return;
      this.reload = true;
      setTimeout(() => {
        this.reload = false;
      }, 500);
      this.$i18n.locale = newValue;
      this.moment.locale(newValue);
    },
  },
};
</script>
<style lang="scss">
.rtl {
  direction: rtl;
}
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  max-width: 1080px;
  margin: auto;
}

#nav {
  padding: 30px;
  display: flex;
  div {
    flex: 1;
  }
  a {
    font-weight: bold;
    color: #2c3e50;

    &.router-link-exact-active {
      color: #42b983;
    }
  }
}
@media print {
  #app {
    transform: scale(0.9);
  }
  #nav {
    display: none;
  }
}
</style>
