<template>
  <div class="home">
    <p v-if="!savedSetting">
      {{ $t("selectAndSave") }}
    </p>
    <h1>{{ $t("prayerTimes") }}</h1>
    <div class="btns-control">
      <button @click="nextMonth">{{ $t("nextMonth") }}</button>
      <button @click="thisMonth">{{ $t("thisMonth") }}</button>
      <button @click="lastMonth">{{ $t("lastMonth") }}</button>
      <button @click="printPage">{{ $t("print") }}</button>
    </div>
    <table v-if="savedSetting && savedSetting.location">
      <thead>
        <tr>
          <th colspan="7">
            {{ $t("city") }}: {{ savedSetting.location.formatted_address }}
          </th>
        </tr>
        <tr>
          <th colspan="7">
            {{ $t("method") + " " + $t(savedSetting.method.key) }}
          </th>
        </tr>
        <tr>
          <th colspan="7">
            {{
              $t("Prayer times for the month of") +
              moment()
                .set({ month: month - 1, year: year })
                .format("MMMM YYYY")
            }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td></td>
          <td
            v-for="(value, key) in prayerTimesForMonth[0].prayerTimes"
            :key="key"
            class="td-head"
            :class="'td-head-' + key"
          >
            {{ $t(key) }}
          </td>
        </tr>
        <tr
          v-for="(pt, idx) in prayerTimesForMonth"
          :key="idx"
          class="tr-day"
          :class="{ 'tr-day-is-friday': pt.isFriday }"
        >
          <td class="td-date">
            {{ moment(pt.date).format("dddd DD MMMM") }}
          </td>
          <td
            v-for="(value, key) in prayerTimesForMonth[idx].prayerTimes"
            :key="key"
            :class="'td-value td-value-' + key"
          >
            {{ value }}
          </td>
        </tr>
        <tr></tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import moment from "moment";

export default {
  name: "Month",
  data() {
    return {
      savedSetting: null,
      prayerTimesForMonth: null,
      year: moment().locale("en").format("YYYY"),
      month: moment().locale("en").format("M"),
    };
  },
  mounted() {
    this.savedSetting = JSON.parse(
      localStorage.getItem("prayer-times-user-settings")
    );
    this.getPrayerTimesForMonth();
  },
  // computed: {
  //   monthYearFormat() {
  //     return moment()
  //       .set({ month: this.month - 1, year: this.year })
  //       .format("MMMM YYYY");
  //   },
  // },
  methods: {
    getPrayerTimesForMonth() {
      const prayerTimesForMonth = [];
      const { location } = this.savedSetting.location.geometry;
      const { method } = this.savedSetting;
      this.prayTimes.setMethod(method);
      // Iterate over each day of the month
      for (
        let day = 1;
        day <= moment(`${this.year}-${this.month}`, "YYYY-MM").daysInMonth();
        day++
      ) {
        const date = moment(
          `${this.year}-${this.month}-${day}`,
          "YYYY-MM-DD"
        )._d;
        const prayerTimes = this.prayTimes.getTimes(
          date,
          [location.lat, location.lng],
          this.userTimeZone.value
        );
        delete prayerTimes.imsak;
        delete prayerTimes.sunset;
        delete prayerTimes.midnight;

        prayerTimesForMonth.push({
          date: date,
          isFriday: moment(date).day() === 5,
          prayerTimes,
        });
      }

      this.prayerTimesForMonth = prayerTimesForMonth;
    },
    nextMonth() {
      if (this.month * 1 >= 12) {
        this.month = 1;
        this.year = this.year * 1 + 1;
      } else {
        this.month = this.month * 1 + 1;
      }
      this.getPrayerTimesForMonth();
    },
    lastMonth() {
      if (this.month * 1 <= 1) {
        this.month = 12;
        this.year = this.year * 1 - 1;
      } else {
        this.month = this.month * 1 - 1;
      }
      this.getPrayerTimesForMonth();
    },
    thisMonth() {
      this.year = moment().locale("en").format("YYYY");
      this.month = moment().locale("en").format("M");
      this.getPrayerTimesForMonth();
    },
    printPage() {
      window.print();
    },
  },
};
</script>
