<template>
  <div class="home">
    <p v-if="!savedSetting">
      You must choose the settings to show prayer times
    </p>
    <h1>{{ $t("prayerTimes") }}</h1>
    <table v-if="savedSetting && savedSetting.location">
      <thead>
        <tr>
          <th colspan="2">
            {{ $t("city") }}: {{ savedSetting.location.formatted_address }}
          </th>
        </tr>
        <tr>
          <th colspan="2">
            {{ $t("method") }}: {{ $t(savedSetting.method.key) }}
          </th>
        </tr>
        <tr>
          <th colspan="2">
            {{ $t("Prayer times for today") }}
            {{ moment().format("dddd DD MMMM YYYY") }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(ut_item, key) in userTimes"
          :key="key"
          :key-data="key"
          :class="ut_item.currentTime ? 'isActive' : ''"
        >
          <td width="250px" v-if="!ut_item.currentTime">
            {{ $t(ut_item.timeName) }}
          </td>
          <td width="250px" v-if="!ut_item.currentTime">
            {{ ut_item.timeObject.format("HH:mm") }}
          </td>

          <td width="250px" v-if="ut_item.currentTime">
            {{ $t("currentTime") }}
          </td>
          <td width="250px" v-if="ut_item.currentTime">
            {{ moment().format("HH:mm") }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import moment from "moment";

export default {
  name: "Home",
  data() {
    return {
      savedSetting: null,
      userTimes: null,
      prayerTimesForMonth: null,
    };
  },
  mounted() {
    this.savedSetting = JSON.parse(
      localStorage.getItem("prayer-times-user-settings")
    );
    this.getUserTimes();
    setInterval(() => {
      this.getUserTimes();
    }, 10000);
  },
  methods: {
    getUserTimes() {
      const { location } = this.savedSetting.location.geometry;
      const { method } = this.savedSetting;
      this.prayTimes.setMethod(method);
      const userTimes = this.prayTimes.getTimes(
        new Date(),
        [location.lat, location.lng],
        this.userTimeZone.value
      );
      delete userTimes.imsak;
      delete userTimes.sunset;
      delete userTimes.midnight;
      const mappedTimes = Object.entries(userTimes).map(
        ([timeName, timeValue]) => {
          return {
            timeName,
            timeValue,
            timeObject: moment(timeValue, "HH:mm"),
          };
        }
      );

      mappedTimes.push({
        timeName: "",
        timeValue: moment().format("HH:mm"),
        timeObject: moment(),
        currentTime: true,
      });
      mappedTimes.sort((a, b) => a.timeObject.diff(b.timeObject));
      this.userTimes = mappedTimes;
    },
  },
};
</script>
