table,
th,
td {
  border: 1px solid black;
  border-collapse: collapse;
}
table {
  width: 500px;
  margin: auto;
  text-align: center;
}
td {
  padding: 10px 0;
  font-size: 20px;
}
tr.isActive {
  background: rgb(126, 25, 25);
  color: white;
  box-shadow: 2px 2px 15px rgba(1, 1, 1, 0.5);
}
</style>
